.collection-card {
  width: 350px;
  display: flex;
  flex-direction: column;
  padding: var(--m);

  border-radius: var(--br);
  min-height: 300px;

  color: var(--on-color);
}

.collection-card .description {
  flex-grow: 1;
}

.collection-card footer {
  display: flex;
  justify-content: space-between;
}

.collection-grid li:nth-child(4n + 1) > .collection-card {
  background-color: var(--color1);
}
.collection-grid li:nth-child(4n + 2) > .collection-card {
  background-color: var(--color2);
}
.collection-grid li:nth-child(4n + 3) > .collection-card {
  background-color: var(--color3);
}
.collection-grid li:nth-child(4n + 4) > .collection-card {
  background-color: var(--color4);
}
